import React, { useState } from "react";
import "../AppML.css";
import { AppMLContentList } from "../../../../Data/Javascript/javascriptContentList";
import { NavLink } from "react-router-dom";
import MenuIcon from '@mui/icons-material/Menu'

function contentList(AppMLContentList) {
  return (
    <>
      <h3>{AppMLContentList.course}</h3>
      <NavLink
      style={{ color: "black", textDecoration: "none" }}
      to={AppMLContentList.path}
    >
      <div className="side-item">{AppMLContentList.title}</div>
    </NavLink>
    </>
  );
}

function AppMLSidebar() {
  const [status,setStatus]=useState(false)
  return (
    <>
      <div className="position-fixed d-lg-block  d-none overflow-auto " style={{ height: "91%" }}>
        {AppMLContentList.map(contentList)}
      </div>
      <div className='d-bock d-lg-none position-fixed overflow-auto '  onClick={()=>setStatus(!status)}><MenuIcon/></div>
      {status?
      <div className="col d-block d-lg-none position-fixed overflow-auto bg-white" style={{ height: "91%" }} onClick={()=>setStatus(!status)}>
      {AppMLContentList.map(contentList)}
    </div>
      :null
       }
    </>
  );
}

export default AppMLSidebar;
