
import React,{useState} from "react";
import "../Kotlin.css";
import { KotlinContentList } from "../../../../Data/Backend/backendContentList";
import { NavLink } from "react-router-dom";
import MenuIcon from '@mui/icons-material/Menu';

function contentList(KotlinContentList) {
  return (
    <>
     <h3>{KotlinContentList.course}</h3>
     <NavLink
      style={{ color: "black", textDecoration: "none" }}
      to={KotlinContentList.path}
    >
      <div className="side-item">{KotlinContentList.title}</div>
    </NavLink>
    </>
  );
}

function KotlinSidebar() {
  const [status,setStatus]=useState(false)
  return (
    <>
      <div className="position-fixed d-lg-block  d-none overflow-auto " style={{ height: "91%" }}>
        {KotlinContentList.map(contentList)}
      </div>
      <div className='d-bock d-lg-none position-fixed overflow-auto '  onClick={()=>setStatus(!status)}><MenuIcon/></div>
      {status?
      <div className="col d-block d-lg-none position-fixed overflow-auto bg-white" style={{ height: "91%" }} onClick={()=>setStatus(!status)}>
      {KotlinContentList.map(contentList)}
    </div>
      :null
       }
    </>
  );
}

export default KotlinSidebar;
