import React, { useState } from "react";
import "../AngularJS.css";
import { AngularJSContentList } from "../../../../Data/Javascript/javascriptContentList";
import { NavLink } from "react-router-dom";
import MenuIcon from '@mui/icons-material/Menu';

function contentList(AngularJSContentList) {
  return (
    <>
      <h3>{AngularJSContentList.course}</h3>
     <NavLink
      style={{ color: "black", textDecoration: "none" }}
      to={AngularJSContentList.path}
    >
      <div className="side-item">{AngularJSContentList.title}</div>
    </NavLink>
    </>
  );
}

function AngularJSSidebar() {
  const [status,setStatus]=useState(false)
  return (
    <>
      
      <div className="position-fixed d-lg-block  d-none overflow-auto " style={{ height: "91%" }}>
        {AngularJSContentList.map(contentList)}
      </div>
      <div className='d-bock d-lg-none position-fixed overflow-auto '  onClick={()=>setStatus(!status)}><MenuIcon/></div>
      {status?
      <div className="col d-block d-lg-none position-fixed overflow-auto bg-white" style={{ height: "91%" }} onClick={()=>setStatus(!status)}>
      {AngularJSContentList.map(contentList)}
    </div>
      :null
       }
    </>
  );
}

export default AngularJSSidebar;
